___CSS_LOADER_EXPORT___.push([module.id, `.workexp-info-entry {
	border: 2px solid #000;
	border-radius: 0.25rem;
	padding: 0.25rem;
    font-family: "Rubik";
    font-size: 1.5vmin;
    text-align: center;
}
.workexp-info-entry img{
	width: 5.5%;
}
#edit-workexp-info-btn {
	width: 5.5%;
	border: none;
	background-color: transparent;
}
#edit-workexp-info-btn img {
	width: 100%;
}
.workexp-info-entry-header {
	display: flex;
	font-family: "Rubik";
	font-size: 2vmin;
	align-items: center;
	justify-content: space-around;
}
.workexp-info-entry-header span{
    width: 100%;
}
.workexp-info-form{
	animation: expand 250ms ease 1;
	transform-origin: top;
}
.toggle-btn-container{
    width: 100%;
    height: 3.5vmin;
    display: flex;
    align-items: center;
    justify-content: left;
    margin-top: 0.75vmin;
}
.toggle-btn-label{
    font-size: 1.5vmin;
    margin-left: 0.5vmin;
    font-family: "Rubik";
}
.toggle-btn{
    background-color: #BBBBBB;
    width: 6.5vmin;
    height: 3.5vmin;
    border-radius: 2vmin;
    cursor: pointer;
    position: relative;
}
.toggle-btn::before{
    position: absolute;
    content: '';
    background-color: #FFF;
    width: 3.25vmin;
    height: 3.25vmin;
    border-radius: 2vmin;
    margin: 0.125vmin;
}
input:checked+.toggle-btn{
    background: var(--primary1);
}
input:checked+.toggle-btn::before{
    transform: translateX(90%);
}
input[type="checkbox"]{
    display: none;
}
.workexp-entry-buttons-space {
	margin-top: 1vmin;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
}
.workexp-entry-button {
	display: flex;
	justify-content: space-around;
	align-items: center;
	min-width: 30%;
}
.workexp-entry-button span {
	font-size: 1.25vmin;
}
.workexp-entry-button img {
	width: 35%;
