___CSS_LOADER_EXPORT___.push([module.id, `.main {
  max-width: 90vw;
  min-height: 80vh;
  padding: 2vmax;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 1fr 2fr;
  gap: 2vmax;
}
.controls-section {
  grid-area: 1 / 1 / 2 / 2;
  display: flex;
  flex-direction: column;
  row-gap: 2.5vmax;
  max-width: 80%;
  height: auto;
}
.edit-section {
  grid-area: 2 / 1 / 3 / 2;
  display: flex;
  flex-direction: column;
  row-gap: 1.5vmin;
  max-width: 100%;
  height: auto;
}
.edit-information-panels {
  max-width: 80%;
  height: 75vh;
  padding: 5%;
  border-radius: 1rem;
  -webkit-box-shadow: 0px 0px 50px 0px rgb(88, 88, 88);
  -moz-box-shadow: 0px 0px 50px 0px rgb(88, 88, 88);
  box-shadow: 0px 0px 50px 0px rgb(88, 88, 88);
  overflow-y: auto;
  background-color: white;
}
#personal-info-panel,
#educational-info-panel,
#workexp-info-panel,
#techskills-info-panel {
  max-width: 100%;
  animation: expand 250ms ease 1;
  transform-origin: top;
  display: flex;
  flex-direction: column;
  row-gap: 0.5vmin;
}
.add-educ-info-btn-space,
.add-workexp-btn-space,
.add-techskills-btn-space {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}
#add-educ-info-btn {
  width: 15%;
  border-radius: 50%;
}
#add-workexp-btn {
  width: 15%;
  border-radius: 50%;
}
#add-educ-info-btn img,
#add-workexp-btn img {
  width: 100%;
}
.preview-section {
  grid-area: 1 / 2 / 3 / 3;
  max-width: 100%;
  min-height: 50vh;
  overflow-y: auto;
  background-color: white;
}
.preview-divider {
  border: 1px solid #adadaddd;
  background-color: #adadaddd;
  width: 100%;
  height: 0.5%;
}
.education-display-space,
.techskills-display-space {
  display: flex;
  flex-direction: column;
}
#education-display-header,
#workexp-display-header,
#techskills-display-header {
  font-size: 1.25vmax;
  text-align: left;
  padding-left: 2.75vmax;
  padding-top: 0.75vmax;
}
.education-info-entries,
.workexp-info-entries,
.techskills-info-entries {
  display: flex;
  flex-wrap: wrap;
  color: #000;
  margin-top: 0.5vmax;
  margin-inline: 2vmax;
  font-family: "Rubik";
}
ul.techskills-list{
  list-style-type: circle;
  margin-left: 5vmin;
  columns: 2;
  -webkit-columns: 2;
  -moz-columns: 2;
  column-fill: auto;
  height: 15vmin;
}

/* ===== Scrollbar CSS ===== */
/* Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #969696 #e6e6e6;
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 14px;
}

*::-webkit-scrollbar-track {
  background: #e6e6e6;
}

*::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 6px;
}
/* ===== Animations ===== */
@keyframes expand {
  0% {
    transform: scaleY(0);
  }

  25% {
    transform: scaleY(0.25);
  }

  50% {
    transform: scaleY(0.5);
  }

  75% {
    transform: scaleY(0.75);
  }

  100% {
    transform: scaleY(1);
  }
}
